
import { Component, ViewChild } from '@angular/core';
import {AlertController,LoadingController, ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-asignacioncalusac101-create',
  templateUrl: 'asignacioncalusac101-create.html',
})
export class Asignacioncalusac101CreatePage {

  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public codigounidad                 : any;
  tiposus: any=[];
  public iddia2		  :any;
  public idhora2		  :any;
  public idperiodo2		  :any=[];
  personalid: string = "";
  public iddia		     : any;
  public idtipo		     : any;
  public idhora		     : any;
  public ididioma		     : any;
  public ididioma2		     : any;
  dataxx: any=[];
ordenpago:any='';
llave:any='';
rubro:any='45';
ano:any='2019'
  public fecha		     = (new Date()).toISOString();
  public identificador		     : any;
  public periodo		     : any;
  items: any=[];
  resultado: any=[];
  idempresa:any;
  public vgrupo  :any=[];
  public vgrupo2  :any=[];
  cui:''

  titulo2:any;
  public vgrupo3  :any=[];
  //private _ID                  : String;
  public pageTitle           	: string;
  public image              	: any;
  novp:any;
  isDisablednit :any;
  constructor( public alertCtrl: AlertController,private loadingCtrl: LoadingController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({

       iddia     : ['', Validators.required],
       idhora     : ['', Validators.required],


     });

     this.titulo2='Sede'

     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value

  })



  }



  ngOnInit() {

    this.storage.get('username').then((value) => {
      this.username = value;
  });

  this.novp =	this.navParams.data.nov;










}

  ionViewDidLoad() : void
  {


    this.dataxx=this.navParams.data.datax



    this.authService.getReg2b( this.dataxx.idtipounidad.id+'/'+ this.dataxx.ididioma._id + '/'+this.dataxx.idperiodo.id  + '/' +'123' +'/dias','unidadplan4s').then((result) => {
      this.vgrupo=result;


      var new_test = [{'nombre': ''}];
new_test[0].nombre=this.vgrupo[0].nombre;

for (var i = 0; i <= this.vgrupo.length - 1; i++) {
var duplicate = false;
	for(var j = new_test.length - 1; j >= 0; j--){
    	if(this.vgrupo[i].nombre == new_test[j].nombre) duplicate = true;
    }
if(!duplicate) new_test.push(this.vgrupo[i]);
}


this.vgrupo=new_test;
    }, (err) => {
    this.msj.showError3('Error',err.error);
    });







  }


  manageGallery2(loading) : void
  {
    let creat='Actualiza';


    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }



    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = {
      operacion:'asigna',

     iddia: this.iddia2,

     idhora: this.idhora2,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' asigna examen de ubicacion '}
    };


          if(options){

            this.authService.createReg2b(this.dataxx._id,options,'asignaubicacions').then((result) => {
              loading.dismiss();
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            loading.dismiss();
            this.msj.showError3('Mensaje',err.error);
          });


        }

  }
  manageGallery() : void
  {



    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');

      return;
    }
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');

      return;
    }



    if(!this.idhora)
    {
      this.msj.showError3('Mensaje','Seleccione una hora valida');

      return;
    }


    if(!this.iddia2)
    {
      this.msj.showError3('Mensaje','Seleccione un dia valido');

      return;
    }




    const loading = this.loadingCtrl.create({
      spinner: 'bubbles',
      content: "Espere un momento,  Realizando  Operación ......"
    });
    loading.present();

    this.manageGallery2(loading);



  }


  cancel2() {
    this.identificador=''
    this.iddia=''
    this.idtipo=''
    this.iddia2=''
    this.idhora=''
    this.nombre=''
    this.items=[]
  }
  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

  onSelectChange(selectedValue: any) {

        this.iddia2=selectedValue

        this.titulo2='una unidad académica'



    this.authService.getReg2b( this.dataxx.idtipounidad.id+'/'+ this.iddia2 + '/'+this.dataxx.idperiodo.id  + '/' +this.dataxx.ididioma._id +'/horas','unidadplan4s').then((result) => {
      this.vgrupo2=result;
    }, (err) => {
    this.msj.showError3('Error',err.error);
    });






  }


  onSelectChange2(selectedValue: any) {
    this.idhora2=selectedValue;
   if(this.iddia==undefined || this.iddia=='')
   {
    this.msj.showError3('Importante','Seleccione sede. ');
    this.idhora2=''
    this.idhora=''




   }

  }




}



